import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 路由懒加载
const Index = () => import('../components/index.vue')
const Recommend = () => import('../components/Recommend/recommend.vue')
const Picture = () => import('../components/Picture/picture.vue')
const Video = () => import('../components/Video/video.vue')
const Audio = () => import('../components/Audio/audio.vue')
const Artist = () => import('../components/Artist/artist.vue')
const Mine = () => import('../components/Mine/mine.vue')
const Account =() => import('../components/Account/account.vue')
const Purchased =() => import('../components/Account/purchased.vue')
const Balance =() => import('../components/Account/balance.vue')
const Binding =() => import('../components/Account/binding.vue')
const Certify =() => import('../components/Account/certify.vue')
const Upgrade =() => import('../components/Account/upgrade.vue')
const Details =() => import('../components/Details/details.vue')

// 滚动到顶部
const scrollBehavior = (to, from, savedPosition) => {

  if (savedPosition) {
    return savedPosition;
  } else {
    const position = {};

    if (to.hash) {
      position.selector = to.hash
    }

    if (to.matched.some(m => m.meta.scrollToTop)) {
      position.x = 0;
      position.y = 0;
    }

    return position;
  }
}

const router = new Router({

    mode: 'history',
    base: __dirname,
    scrollBehavior,
    routes: [
        {
          path: '/',
          name: 'index',
          component: Index,
          redirect: 'recommend',
          children: [
            { path: 'recommend', name: 'recommend', component: Recommend },
            { path: 'picture', name: 'picture', component: Picture },
            { path: 'video', name: 'video', component: Video },
            { path: 'audio', name: 'audio', component: Audio },
            { path: 'details', name: 'details', component: Details },
            { 
              path: 'artist', 
              name: 'artist',
              meta: {
                requireAuth: true
              }, 
              component: Artist 
            },
            { path: 'mine', name: 'mine', component: Mine },
            { 
              path: 'account', 
              name: 'account', 
              component: Account,
              redirect: '/account/certify',
              children: [
                // { path: '/account/purchased', component: Purchased }, // 已购作品
                // { path: '/account/balance', component: Balance }, // 账户余额 
                // { path: '/account/binding', component: Binding }, // 账户绑定
                { path: '/account/certify', component: Certify }, // 艺术家认证
                { path: '/account/upgrade', component: Upgrade }, // 升级为企业号
              ]
            }
          ]
        }
    ],
    linkActiveClass: ""
})

