import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import axios from 'axios'

const isLogin = JSON.parse(localStorage.getItem('isLogin')) || '';

const comments = JSON.parse(localStorage.getItem('comments')) || [];
// const oplaceComments = JSON.parse(localStorage.getItem('oplaceComments')) || [];

const store = new Vuex.Store({
    state: {

        isLogin: isLogin, // 登录状态
        comments: comments, // 全部评论数据
        // oplaceComments: [], //每个作品的评论数据
    },
    mutations: {

        // 记录登录状态
        ISLOGIN (state, isLogin) {
            state.isLogin = isLogin;
            localStorage.setItem("isLogin", JSON.stringify(state.isLogin));
        },

        // 按照作品的id过滤评论 并存储至本地
        FILTERCOMMENTS (state, id) {
            // state.comments.forEach(item => {

            //     if (item.oplaceId == id) {
            //         state.oplaceComments.push(item);
            //         // console.log(oplaceComments);
            //     }
            //     return true;

                
            // });
            // state.comments = state.oplaceComments;
            // console.log(state.comments);
            // localStorage.setItem('comments', JSON.stringify(state.comments));
        },

        // 点赞后计数
        COUNTPRAISE (state, value) {

            state.comments.some(item => {
                if (item.id === value.id) {
                    item.praise = value.praise;
                    item.praiseState = value.praiseState;
                }
            })
            localStorage.setItem('comments', JSON.stringify(state.comments));
        }
    },
    getters: {

    }
});


export default store


